import React, { useEffect, useState } from "react";
import axios from "axios";
import ProgramDetails from "./ProgramDetails";

function InformationSidebar({
  mappointid,
  setOpensidebar,
  selectedLanguage,
  selectedStatuses,
  selectedSupports,
  selectedOrganizations,
}) {
  const [data, setData] = useState([]);
  const [language, setLanguage] = useState(selectedLanguage);
  const [statusid, setStatusid] = useState(selectedStatuses);
  const [supportid, setSupportid] = useState(selectedSupports);
  const [organizationid, setOrganizationid] = useState(selectedOrganizations);

  useEffect(() => {
    setData([]);
    axios
      .post(`/api/programListForFilterEng`, {
        mappointid,
        language,
        statusid,
        supportid,
        organizationid,
      })
      .then((response) => {
        setData(response.data.data);
      })
      .catch((e) => {
        console.log("error");
      });
  }, [mappointid]);

  return (
    <ProgramDetails
      data={data}
      mappointid={mappointid}
      setOpensidebar={setOpensidebar}
    />
  );
}

export default InformationSidebar;
