import React, { useState, useEffect } from "react";

import { YMaps, Map, Clusterer, Placemark } from "react-yandex-maps";
import InformationSidebar from "./InformationSidebar";

function MapPageFunc({
  coordinates,
  selectedLanguage,
  selectedStatuses,
  selectedSupports,
  selectedOrganizations,
}) {
  const [ymaps, setYmaps] = useState(null);
  const [selectedPoint, setSelectedPoint] = useState(null);
  const [mappointid, setMappointid] = useState("");
  const [openSidebar, setOpensidebar] = useState(false);

  const onPlacemarkClick = (point) => () => {
    setSelectedPoint(point);
    setOpensidebar(true);
    setMappointid(point.mappointId);
  };

  async function asyncCall() {
    const result = await onPlacemarkClick();
  }

  useEffect(() => {
    asyncCall();
  }, []);

  const mapState = {
    center: [40.987595, 45.15185],
    zoom: 9,
  };

  const mapOptions = {
    preset: "islands#blueCircleIcon",
    iconColor: "#05558F",
  };

  function of(c) {
    const mapProperties = {
      iconContent: `<i>${c}</i>`,
    };
    return mapProperties;
  }

  return (
    <div className="maps">
      <YMaps query={{ lang: "ru_RU", load: "package.full" }}>
        <Map
          className="map"
          defaultState={mapState}
          onLoad={(ymaps) => setYmaps({ ymaps })}
        >
          <Clusterer>
            {ymaps &&
              coordinates.map((point, index) => (
                <Placemark
                  options={mapOptions}
                  properties={of(point.programCount)}
                  key={index}
                  geometry={[point.latitude, point.longitude]}
                  onClick={onPlacemarkClick(point)}
                />
              ))}
          </Clusterer>
        </Map>
      </YMaps>
      {openSidebar && (
        <InformationSidebar
          selectedSupports={selectedSupports}
          selectedOrganizations={selectedOrganizations}
          selectedStatuses={selectedStatuses}
          mappointid={mappointid}
          setOpensidebar={setOpensidebar}
          selectedLanguage={selectedLanguage}
        />
      )}
    </div>
  );
}

export default MapPageFunc;
