import React, { useState, useEffect } from "react";
import "./map.css";
import City from "./componenets/Filter/City/City";
import Language from "./componenets/Filter/Language/Language";
import Status from "./componenets/Filter/Status/Status";
import Support from "./componenets/Filter/Support/Support";
import MapPageFunc from "./MapPageFunc";
import Organization from "./componenets/Filter/Organization/Organization";

const Select = ({ setInfo }) => {
  const [cities, setCities] = useState([]);
  const [selectedCities, setSelectedCities] = useState([]);
  const [coordinates, setCoordinates] = useState([]);
  const [statuses, setStatuses] = useState([]);
  const [selectedStatuses, setSelectedStatuses] = useState([]);
  const [supports, setSupports] = useState([]);
  const [selectedSupports, setSelectedSupports] = useState([]);
  const [organizations, setOrganizations] = useState([]);
  const [selectedOrganizations, setSelectedOrganizations] = useState([]);
  const [selectedLanguage, setSelectedLanguage] = useState(
    localStorage.getItem("lang")
  );

  useEffect(() => {
    const lang = localStorage.getItem("lang");
    if (lang) {
      setSelectedLanguage(lang);
    } else {
      localStorage.setItem("lang", "arm");
    }
  }, []);

  useEffect(() => {
    fetch("/api/filterEng", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        communityid: selectedCities,
        statusid: selectedStatuses,
        supportid: selectedSupports,
        organizationid: selectedOrganizations,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setCoordinates(res.data);
      });
  }, [
    selectedCities,
    selectedStatuses,
    selectedSupports,
    selectedOrganizations,
  ]);

  useEffect(() => {
    fetch("/api/communities", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        language: selectedLanguage,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setCities(res.data);
      });

    fetch("/api/statuses", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        language: selectedLanguage,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setStatuses(res.data);
      })
      .catch((err) => {
        console.log(err);
      });

    fetch("/api/organizations", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        language: selectedLanguage,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setOrganizations(res.data);
      });

    fetch("/api/supportsList", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        language: selectedLanguage,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        let a = res.data;
        a.forEach((el) => {
          el.active = false;
        });
        setSupports(a);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [selectedLanguage]);

  return (
    <>
      <div style={{ display: "flex", flexWrap: "wrap" }}>
        <MapPageFunc
          selectedSupports={selectedSupports}
          selectedStatuses={selectedStatuses}
          coordinates={coordinates}
          setInfo={setInfo}
          selectedLanguage={selectedLanguage}
          selectedOrganizations={selectedOrganizations}
        />
        <div className="filters">
          <City
            selectedCities={selectedCities}
            setSelectedCities={setSelectedCities}
            cities={cities}
            selectedLanguage={selectedLanguage}
          />
          <Support
            selectedSupports={selectedSupports}
            setSelectedSupports={setSelectedSupports}
            supports={supports}
            setSupports={setSupports}
            selectedLanguage={selectedLanguage}
          />
          <Status
            selectedStatuses={selectedStatuses}
            setSelectedStatuses={setSelectedStatuses}
            statuses={statuses}
            selectedLanguage={selectedLanguage}
          />
          <Organization
            selectedOrganizations={selectedOrganizations}
            setSelectedOrganizations={setSelectedOrganizations}
            organizations={organizations}
            selectedLanguage={selectedLanguage}
          />
          <Language
            selectedLanguage={selectedLanguage}
            setSelectedLanguage={setSelectedLanguage}
          />
        </div>
      </div>
    </>
  );
};

export default Select;
