import React, { useEffect, useState, useRef } from "react";
import "../../../map.css";
import useOutsideClick from "../../../useOutsideClick";

const City = ({
  cities,
  selectedCities,
  setSelectedCities,
  selectedLanguage,
}) => {
  const [checked, setChecked] = useState();

  const [language, setLanguage] = useState("");

  const [show, setSate] = useState(false);
  const ref = useRef();

  useOutsideClick(ref, () => {
    if (show) setSate(false);
  });

  const handleOptionChange1 = (i) => {
    setChecked(i);
    if (selectedCities.some((c) => c === i)) {
      let arr = selectedCities.filter((x) => x !== i);
      setSelectedCities(arr);
    } else {
      setSelectedCities([...selectedCities, i]);
    }
  };

  useEffect(() => {
    if (selectedLanguage == "arm") {
      setLanguage("Համայնք");
    } else {
      setLanguage("Community");
    }
  });

  return (
    <div className="community">
      <button
        className="btnSupport"
        id="btnSelect"
        onClick={() => setSate(!show)}
      >
        <label className="support">{language}</label>
        <img
          src={require("../../../icon/arrow.svg").default}
          onClick={() => setSate(!show)}
        />
      </button>
      {show && (
        <div ref={ref} className="NestedSelect">
          <div className="list city">
            {cities.map((i) => (
              <li
                className="li1"
                key={i.id}
                onClick={() => handleOptionChange1(i.id)}
                style={{
                  backgroundColor: selectedCities.some((item) => item === i.id)
                    ? "#A4C2D8"
                    : "#FAFAFA",
                }}
              >
                {i.name}
              </li>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default City;
