import React, { useState, useEffect, useRef } from "react";
import "../../../map.css";
import useOutsideClick from "../../../useOutsideClick";

const Support = ({
  supports,
  setSupports,
  selectedSupports,
  setSelectedSupports,
  selectedLanguage,
}) => {
  const [openCategory, setOpenCategory] = useState([]);

  const [show, setSate] = useState(false);
  const ref = useRef();

  useOutsideClick(ref, () => {
    if (show) setSate(false);
  });

  const selectSupport = (supportId) => {
    const arr = selectedSupports.filter((item) => item != supportId);
    if (arr.length == selectedSupports.length) {
      selectedSupports.push(supportId);
      setSelectedSupports([...selectedSupports]);
    } else {
      setSelectedSupports([...arr]);
    }
  };

  const checkCategory = (e, index) => {
    const checked = e.target.checked;
    const items = supports[index].support;
    if (checked) {
      for (let i = 0; i < items.length; i++) {
        const ind = selectedSupports.some((item) => item == items[i].supportid);
        if (!ind) {
          selectedSupports.push(items[i].supportid);
        }
      }

      setSelectedSupports([...selectedSupports]);
    } else {
      let n = selectedSupports;
      for (let i = 0; i < items.length; i++) {
        const ind = n.findIndex((item) => item == items[i].supportid);
        if (ind >= 0) {
          n.splice(ind, 1);
        }
      }
      setSelectedSupports([...n]);
    }
    supports[index].active = !supports[index].active;
    setSupports([...supports]);
  };

  const openCategories = (id) => {
    if (openCategory.some((item) => item === id)) {
      let index = openCategory.findIndex((item) => item === id);
      openCategory.splice(index, 1);
      setOpenCategory([...openCategory]);
    } else {
      openCategory.push(id);
      setOpenCategory([...openCategory]);
    }
  };

  return (
    <div className="project_name">
      <button
        className="btnSupport"
        id="btnSelect"
        onClick={() => setSate(!show)}
      >
        <label className="support">
          {selectedLanguage == "arm" ? "Ոլորտ" : "Sphere"}
        </label>
        <img
          src={require("../../../icon/arrow.svg").default}
          onClick={() => setSate(!show)}
        />
      </button>
      {show && (
        <div ref={ref} className="nested">
          {supports.map((category, index) => (
            <div key={index} className="list3">
              <ul className="ul3">
                <div className="supportList">
                  <input
                    type="checkbox"
                    id="check"
                    checked={category.active}
                    className="checkbox1"
                    onChange={(e) => checkCategory(e, index)}
                  />
                </div>
                <label
                  className="category_name"
                  onClick={(e) => openCategories(category.categoryid)}
                >
                  {category.category} ({category.support.length})
                </label>
                <img
                  className="arrowSelect"
                  src={require("../../../icon/arrow.svg").default}
                  onClick={(e) => openCategories(category.categoryid)}
                />
                {openCategory.some((item) => item === category.categoryid) ? (
                  <div className="support_types">
                    {category.support.map((support, index) => (
                      <li
                        key={index}
                        style={{
                          backgroundColor: selectedSupports.some(
                            (item) => item === support.supportid
                          )
                            ? "#A4C2D8"
                            : "#FAFAFA",
                        }}
                        className="li3"
                        onClick={() => selectSupport(support.supportid)}
                      >
                        {support.name}
                      </li>
                    ))}
                  </div>
                ) : null}
              </ul>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};
export default Support;
