import React from "react";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import Select from "./maps";

function Routes() {
  return (
    <div>
      <BrowserRouter>
        <Switch>
          <Route path="/home" component={Select} exact />
          <Redirect to="/home" />
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default Routes;
