import React, { useState, useEffect, useRef } from "react";
import "../../../map.css";
import useOutsideClick from "../../../useOutsideClick";

const Status = ({
  statuses,
  selectedStatuses,
  setSelectedStatuses,
  selectedLanguage,
}) => {
  const [checked, setChecked] = useState();

  const [language, setLanguage] = useState("");

  const [show, setSate] = useState(false);
  const ref = useRef();

  useOutsideClick(ref, () => {
    if (show) setSate(false);
  });

  useEffect(() => {
    if (selectedLanguage == "arm") {
      setLanguage("Ստատուս");
    } else {
      setLanguage("Status");
    }
  });

  const handleOptionChange1 = (i) => {
    setChecked(i);
    if (selectedStatuses.some((c) => c === i)) {
      let arr = selectedStatuses.filter((x) => x !== i);
      selectedStatuses = arr;
    } else {
      selectedStatuses.push(i);
    }
    setSelectedStatuses([...selectedStatuses]);
  };

  return (
    <div className="status">
      <button
        className="btnSupport"
        id="btnSelect"
        onClick={() => setSate(!show)}
      >
        <label className="support">{language}</label>
        <img
          src={require("../../../icon/arrow.svg").default}
          onClick={() => setSate(!show)}
        />
      </button>
      {show && (
        <div ref={ref} className="NestedStatus">
          <div className="statuses">
            {statuses.map((i) => (
              <li
                className="li2"
                key={i.id}
                onClick={() => handleOptionChange1(i.id)}
                style={{
                  backgroundColor: selectedStatuses.some(
                    (item) => item === i.id
                  )
                    ? "#A4C2D8"
                    : "#FAFAFA",
                }}
              >
                {i.status}
              </li>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default Status;
